body{
    -webkit-font-smoothing: antialiased;
    text-rendering: optimizeLegibility;
    font-family: "AzoSans","Avenir","Helvetica Neue",helvetica,sans-serif;
}

.home {
    background: url("../assets/home_image.jpg") center center no-repeat;
    background-size:   cover;
    position: relative;
    border-top: 1px solid #6585ed;
    border-bottom: 1px solid #6585ed;
}

.description{
    padding: 40px 10px 10px 10px;
    max-width: 900px;
    margin: 0 auto;
}

.description p{
    font-size: 14px;
    color: #8e97a9;
}

.icon{
    padding: 20px;
    width: 120px;
    height: 120px;
    border: 1px solid #e2e6eb;
    border-radius: 30px;
    background-color: white;
}

section {
    min-height: 400px;
    background-color: #f5f6f8;
}

.section-team {
    background-color: #29314b;
    color:white;
}

.logo {
  background: url("../assets/kevinclaeys_logo.png") center center no-repeat;
  width: 100px;
  height: 70px;
  float: left;
  margin-top: 5px;
}

.desc {
    max-width: 500px;
    margin: 0 auto;
}

.desc h1{
    font-size: 40px;
}

.desc p {
    font-size: 17px;
}

.blogpost, .paginatoritem {
  list-style-type: none;
}

.blogpost p {
  margin-bottom: 0;
}

.blogpost h3 {
  margin-top: 0;
}

.achievements-and-projects li{
  list-style-type: none;
}
